.content__element {
  margin-left: auto;
  margin-right: auto;
  width: calc(100% - 2 * 200px);
}

@media screen and (max-width: 1024px) {
  .content__element {
    width: calc(100% - 2 * 72px);
  }
}

@media screen and (max-width: 768px) {
  .content__element {
    width: calc(100% - 2 * 19px);
  }
}
